












































































.reserve {
  width: 100vw;
  background-color: #f9f9f9;
  .article {
    width: 95%;
    margin: 0 auto;
    height: 66.6vh;
    padding: 1vw 2vw;
    overflow-y: scroll;
    box-sizing: border-box;
    background-color: white;
    font-size: 4vw;
    p {
      text-indent: 30px;
    }
    div {
      margin: 2vw 0;
      img {
        width: 100%;
      }
    }
  }
}
